/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    color: $widget-text-color;
    line-height: $widget-list-item-height;
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    &:last-child {
        margin: 0;
    }

    .mkdf-widget-title-holder {
        padding-bottom: $widget-list-item-padding;

        .mkdf-widget-title {
            color: $widget-heading-color;
            margin-bottom: $widget-title-margin;
            margin-top: 0;
        }
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;

            &:last-child {
                padding-bottom: 0;
            }
        }

        .children,
        .sub-menu {
            padding-top: $widget-list-item-padding;
        }
    }

    a {
        color: $widget-link-color;

        &:hover {
            color: $widget-link-hover-color;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    .textwidget {
        p:first-child {
            margin-top: 0;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // calendar

    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            color: $widget-heading-color;
            padding: 3px 4px;
        }

        td#today {
            background-color: $widget-list-item-background-color;
            color: $widget-text-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // rss

    &.widget_rss {
        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search

    .mkdf-searchform {
        .input-holder {
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 13px;
                font-weight: inherit;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;

                @include mkdfTransition(color .2s ease-in-out);
            }

            input.search-field {
                padding: 0 $input-padding;
                width: calc(100% - #{$widget-search-form-height});

                @include mkdfPlaceholder {
                    color: $widget-text-color;

                    @include mkdfTransition(color 0.2s ease-in-out);
                }

                &:focus {
                    @include mkdfPlaceholder {
                        color: transparent;
                    }
                }
            }

            button {
                background: transparent;
                color: $widget-text-color;
                cursor: pointer;
                font-size: 18px;
                padding: 0;
                width: $widget-search-form-height;

                @include mkdfTransition(color .2s ease-in-out);

                &:hover {
                    background: transparent;
                    color: $widget-link-hover-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // combobox
    // @see https://en.wikipedia.org/wiki/combo_box

    &.widget_archive,
    &.widget_categories,
    &.widget_text {
        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-radius: 0;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include mkdfTransition(color .2s ease-in-out);

            option {
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tag cloud

    .wp-block-tag-cloud,
    .tagcloud { // target only tagcloud class, it's the same as in woo tagcloud widget
        padding-top: $widget-list-item-padding;

        a {
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: inherit !important;
            font-weight: inherit;
            line-height: $widget-tag-cloud-height;
            padding: 0;
            text-transform: lowercase;

            &:after {
                content: ",";
            }

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // social icon widget

    &.mkdf-social-icons-group-widget {
        &.mkdf-square-icons {
            .mkdf-social-icon-widget-holder {
                color: $widget-link-color;
                background-color: $widget-list-item-background-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }

            &.mkdf-light-skin {
                .mkdf-social-icon-widget-holder {
                    color: $default-text-color;
                    background-color: #ffffff;

                    &:hover {
                        color: $default-text-color;
                        background-color: $first-main-color;
                    }
                }
            }
        }

        .mkdf-social-icon-widget-holder {
            &:after {
                color: $widget-text-color !important;
            }
        }
    }
}


/* ==========================================================================
# Block widget search style - start
========================================================================== */

.widget_block.widget_search {

    .input-holder {
        border-color: $widget-list-item-border-color;
        border-style: $widget-list-item-border-style;
        border-width: $widget-list-item-border-width;

        input,
        button {
            background-color: transparent;
            border: 0;
            border-radius: 0;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            float: left;
            font-family: $default-text-font;
            font-size: 14px;
            font-weight: inherit;
            line-height: calc(#{$widget-search-form-height} - 2px);
            outline: 0;
            position: relative;
            vertical-align: top;
            -webkit-appearance: none;

            @include mkdfTransition(color .2s ease-in-out);
        }

        button {
            background: $widget-list-item-background-color;
            color: $widget-text-color;
            cursor: pointer;
            font-size: 16px;
            width: $widget-search-form-height;

            @include mkdfTransition(color .2s ease-in-out);

            &:hover {
                color: $widget-link-hover-color;
            }

            span {
                display: block;
                left: 0;
                line-height: inherit;
                position: absolute;
                top: 0;
                width: 100%;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }
    }

    .mkdf-search-form-label {
        display: block;
        margin-bottom: 8px;
        color: #2a2a2a;
    }

    .mkdf-search-form-inner {
        position: relative;
        width: 100%;
        align-items: stretch;
        display: flex;
    }

    .mkdf-search-form-field {
        width: 100%;
        vertical-align: top;
        border-radius: 0;
        border-style: solid;
        border-width: 1px;
        box-sizing: border-box;
        cursor: text;
        color: #000;
        outline: 0;
        padding: 12px 20px;
        line-height: inherit;
        letter-spacing: inherit;
        background-color: transparent;
        font-weight: 300;
        font-family: $default-text-font;
        height: 43px;

        &::placeholder {
            color: #000;
        }

        &:focus {
            color: #000;
        }
    }

    .mkdf-search-form-button {
        align-items: center;
        display: inline-flex;
        flex-shrink: 0;
        font-family: $default-text-font;
        font-size: 16px;
        color: #939393;
        text-transform: uppercase;
        cursor: pointer;

        &.mkdf--has-icon {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            width: 43px;
            height: 100%;
            padding: 12px 0;
            border: 0;

            i.fa.fa-search {
                font-size: 18px;
                line-height: 26px;
                color: #2a2a2a;
            }
        }

        // position
        &.mkdf--button-outside {
            margin-left: 20px;
            border: 1px solid #edeff2;
            background-color: #edeff2;
        }

        &.mkdf--button-inside {
            background-color: transparent !important;
            border: none !important;
            height: 100%;
            position: absolute;
            right: 0;
            top: 0;
        }
    }

    .mkdf-light-header & {

        .mkdf-search-form-label {
            color: $header-light-color !important;
        }

        .mkdf-search-form-field {
            border-color: $header-light-color !important;

            &::placeholder {
                color: $header-light-color !important;
            }
        }

        .mkdf-search-form-button {
            color: $header-light-color !important;

            &.mkdf--has-icon {

                i.fa.fa-search {
                    color: $header-light-color !important;
                }
            }

            &.mkdf--button-outside {
                border-color: #2a2a2a;
                background-color: #2a2a2a;
            }
        }
    }

    .mkdf-dark-header & {

        .mkdf-search-form-label {
            color: $header-dark-color !important;
        }

        .mkdf-search-form-field {
            border-color: $header-dark-color !important;

            &::placeholder {
                color: $header-dark-color !important;
            }
        }

        .mkdf-search-form-button {
            color: $header-dark-color !important;

            &.mkdf--has-icon {

                i.fa.fa-search {
                    color: $header-dark-color !important;
                }
            }
        }
    }
}

/* ==========================================================================
# Block widget search style - end
========================================================================== */
